#playRF #countdown {
	position: relative;
	margin: auto;
	height: 40px;
	width: 40px;
	text-align: center;
}

#playRF #countdown-number {
	color: #333;
	display: inline-block;
	line-height: 40px;
}

#playRF svg {
	position: absolute;
	top: 0;
	right: 0;
	width: 40px;
	height: 40px;
	transform: rotateY(-180deg) rotateZ(-90deg);
}

#playRF svg circle {
	stroke-dasharray: 113px;
	stroke-dashoffset: 0px;
	stroke-linecap: round;
	stroke-width: 2px;
	stroke: #333;
	fill: none;
	animation: countdown 10s linear infinite forwards;
}

@keyframes countdown {
	from {
		stroke-dashoffset: 0px;
	}
	to {
		stroke-dashoffset: 113px;
	}
}
