@import url(//fonts.googleapis.com/css?family=Vibur);

.logo {
	text-align: center;
	width: auto;
	height: 1vw;
	margin: auto;
	position: relative;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	user-select: none;
}

.logo b {
	font: 400 65px "Vibur";
	color: #fee;
	text-shadow: 0 0 2px, 0 0 1em #ff4444, 0 0 0.5em #ff4444, 0 0 0.1em #ff4444, 0 5px 9px #000;
}
.logo b span {
	animation: blink linear infinite 2s;
}
.logo b span:nth-of-type(2) {
	animation: blink linear infinite 3s;
}
.logo b span:nth-of-type(3) {
	animation: blink linear infinite 4s;
}
@keyframes blink {
	78% {
		color: inherit;
		text-shadow: inherit;
	}
	79% {
		color: #333;
	}
	80% {
		text-shadow: none;
	}
	81% {
		color: inherit;
		text-shadow: inherit;
	}
	82% {
		color: #333;
		text-shadow: none;
	}
	83% {
		color: inherit;
		text-shadow: inherit;
	}
	92% {
		color: #333;
		text-shadow: none;
	}
	92.5% {
		color: inherit;
		text-shadow: inherit;
	}
}
