/* Tiles */

.tiles {
	display: -moz-flex;
	display: -ms-flex;
	display: flex;
	flex-wrap: wrap;
	position: relative;
	margin: -2.5em 0 0 -2.5em;
}

.tiles article {
	transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
	transition: transform 0.5s ease, opacity 0.5s ease;
	transition: transform 0.5s ease, opacity 0.5s ease, -webkit-transform 0.5s ease;
	position: relative;
	width: calc(33.33333% - 6.5em);
	margin: 2.5em 1.6em 1.6em 2.5em;
}

.tiles article > .image {
	transition: -webkit-transform 0.5s ease;
	transition: transform 0.5s ease;
	transition: transform 0.5s ease, -webkit-transform 0.5s ease;
	position: relative;
	display: block;
	width: 100%;
	border-radius: 4px;
	overflow: hidden;
}

.tiles article > .image img {
	display: block;
	width: 323px;
	height: 326px;
	background-size: cover;
}

.tiles article > .image:before {
	pointer-events: none;
	transition: background-color 0.5s ease, opacity 0.5s ease;
	content: "";
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 1;
	z-index: 1;
	opacity: 0.8;
}

.tiles article > .image:after {
	pointer-events: none;
	transition: opacity 0.5s ease;
	content: "";
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='100' height='100' viewBox='0 0 100 100' preserveAspectRatio='none'%3E%3Cstyle%3Eline %7B stroke-width: 0.25px%3B stroke: %23ffffff%3B %7D%3C/style%3E%3Cline x1='0' y1='0' x2='100' y2='100' /%3E%3Cline x1='100' y1='0' x2='0' y2='100' /%3E%3C/svg%3E");
	background-position: center;
	background-repeat: no-repeat;
	background-size: 100% 100%;
	opacity: 0.25;
	z-index: 2;
}

.tiles article > a {
	display: -moz-flex;
	display: -ms-flex;
	display: flex;
	-moz-flex-direction: column;
	flex-direction: column;
	-moz-align-items: center;
	-ms-align-items: center;
	align-items: center;
	-moz-justify-content: center;
	-ms-justify-content: center;
	justify-content: center;
	transition: background-color 0.5s ease, -webkit-transform 0.5s ease;
	transition: background-color 0.5s ease, transform 0.5s ease;
	transition: background-color 0.5s ease, transform 0.5s ease, -webkit-transform 0.5s ease;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 1em;
	border-radius: 4px;
	border-bottom: 0;
	color: #ffffff;
	text-align: center;
	text-decoration: none;
	z-index: 3;
}

.tiles article > a > :last-child {
	margin: 0;
}

.tiles article > a:hover {
	color: #ffffff !important;
}

.tiles article > a h1 {
	margin: 0;
	color: black;
	opacity: 0.8;
}

.tiles article > a .content {
	transition: max-height 0.5s ease, opacity 0.5s ease;
	width: 100%;
	max-height: 0;
	line-height: 1.5;
	margin-top: 0.35em;
	opacity: 0;
}

.tiles article > a .content > :last-child {
	margin-bottom: 0;
}

.tiles article.style1 > .image:before {
	/* background-color: #4158d0; */
	background-image: linear-gradient(43deg, #4158d0 0%, #c850c0 46%, #ffcc70 100%);
	opacity: 0.8;
}

.tiles article.style2 > .image:before {
	background-color: #7ecaf6;
	opacity: 0.8;
}

.tiles article.style3 > .image:before {
	background-color: #7bd0c1;
}

.tiles article.style4 > .image:before {
	background-color: #c75b9b;
}

.tiles article.style5 > .image:before {
	background-color: #ae85ca;
}

.tiles article.style6 > .image:before {
	background-color: #8499e7;
}

body:not(.is-touch) .tiles article:hover > .image {
	-webkit-transform: scale(1.1);
	transform: scale(1.1);
}

body:not(.is-touch) .tiles article:hover > .image:before {
	background-color: #333333;
	opacity: 0.35;
}

body:not(.is-touch) .tiles article:hover > .image:after {
	opacity: 0;
}

body:not(.is-touch) .tiles article:hover .content {
	max-height: 15em;
	opacity: 1;
}

* + .tiles {
	margin-top: 2em;
}

body.is-preload .tiles article {
	-webkit-transform: scale(0.9);
	transform: scale(0.9);
	opacity: 0;
}

body.is-touch .tiles article .content {
	max-height: 15em;
	opacity: 1;
}

