#indexrf .card .card_image img {
    width: inherit;
    height: inherit;
    border-radius: 40px;
    object-fit: cover;
}

#indexrf .card .card_title {
    text-align: center;
    border-radius: 0px 0px 40px 40px;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 30px;
    margin-top: -245px;
    height: 40px;
    color: #79d6ed;
}

#indexrf .card:hover {
    -webkit-transform: scale(0.9, 0.9);
            transform: scale(0.9, 0.9);
    box-shadow: 5px 5px 30px 15px rgba(0,0,0,0.25),
      -5px -5px 30px 15px rgba(0,0,0,0.22);
}

/* #indexrf .title-white {
    color: #3079d9;
} */

#indexrf .title-black {
    color: black;
}

@media all and (max-width: 500px) {
    #indexrf .card-list {
      /* On small screens, we are no longer using row direction but column */
