    }
}

:root {
	--dark: #110c22;
	--bg: #f2f7ff;
	--primary: hsl(224, 100%, 62%);
	--primary-dark: hsl(224, 100%, 65%);
	--primary-light: hsl(224, 100%, 90%);
	--primary-lighter: hsl(224, 100%, 95%);
	--white: #ffffff;
	--grey: hsl(0, 0%, 95%);
	--grey-dark: hsl(0, 0%, 85%);
	--grey-light: hsl(0, 0%, 97%);
	--grey-lighter: hsl(0, 0%, 98%);
	--green: hsl(153, 100%, 22%);
	--green-light: hsl(153, 100%, 95%);
	--red: hsl(0, 97%, 37%);
	--red-light: hsl(0, 97%, 95%);
	--ff-titles: "Merriweather", serif;
	--ff-body: "IBM Plex Sans", sans-serif;
}

/* body {
	color: var(--dark);
	min-height: 100vh;
	background: var(--bg);
	display: grid;
} */

#feedback main {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 24px;
	flex-direction: column;
	margin: 32px auto;
	max-width: 1024px;
	width: 100%;
}

#feedback a {
	text-decoration: none;
	color: inherit;
}

#feedback a.link {
	text-decoration: none;
	color: hsl(224, 100%, 62%);
	color: var(--primary);
	border-bottom: 1px solid hsl(224, 100%, 62%);
	border-bottom: 1px solid var(--primary);
}

#feedback a.link:hover {
	text-decoration: none;
	color: hsl(224, 100%, 62%);
	color: var(--primary);
	border-bottom: 1px solid transparent;
}

#feedback h1,
#feedback h3 {
	margin-bottom: 16px;
}

#feedback button {
	font-size: 16px;
	padding: 24px;
	margin: 16px 8px 16px 0;
	line-height: 0;
	width: 100%;
	vertical-align: top;
	cursor: pointer;
	border: 0;
	border-radius: 5px;
	font-family: "IBM Plex Sans", sans-serif;
	font-family: var(--ff-body);
	font-weight: 500;
	min-width: 150px;
	max-width: -webkit-max-content;
	max-width: max-content;
	letter-spacing: 1px;
	transition: all 0.5s ease;
	white-space: nowrap;
}

#feedback .btn-primary {
	color: #ffffff;
	color: var(--white);
	background: hsl(224, 100%, 62%);
	background: var(--primary);
	border: 1px solid hsl(224, 100%, 62%);
	border: 1px solid var(--primary);
	box-shadow: 0px 21px 21px -6px transparent;
}

#feedback .btn-primary:hover {
	border: 1px solid hsl(224, 100%, 62%);
	border: 1px solid var(--primary);
	background: hsl(224, 100%, 65%);
	background: var(--primary-dark);
	color: #ffffff;
	color: var(--white);
	box-shadow: 0px 21px 21px -6px hsl(224, 100%, 90%);
	box-shadow: 0px 21px 21px -6px var(--primary-light);
}

#feedback .btn-outline {
	background: #ffffff;
	background: var(--white);
	border: 2px solid #dcdee9;
}

#feedback .btn-outline:hover {
	background: hsl(0, 0%, 95%);
	background: var(--grey);
	color: #110c22;
	color: var(--dark);
	box-shadow: 0px 21px 21px -6px hsl(0, 0%, 95%);
	box-shadow: 0px 21px 21px -6px var(--grey);
}

#feedback .btn-dark {
	border-radius: 50px;
	padding: 24px;
	background: #110c22;
	background: var(--dark);
	color: #ffffff;
	color: var(--white);
	border: 1px solid transparent;
}

#feedback .btn-dark:hover {
	color: #110c22;
	color: var(--dark);
	background: #ffffff;
	background: var(--white);
	border: 1px solid #110c22;
	border: 1px solid var(--dark);
}

#feedback .card-layout {
	position: relative;
	background: #ffffff;
	background: var(--white);
	box-shadow: 0px 22px 20px -17px rgba(41, 50, 100, 0.146715);
	border-radius: 15px;
	width: 100%;
	text-align: center;
	margin: 32px 0;
}

#feedback .content {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	flex-direction: column;
}

#feedback .title {
	font-family: "Merriweather", serif;
	font-family: var(--ff-titles);
	font-style: normal;
	font-weight: bold;
	font-size: 22px;
	line-height: 160%;
	margin: 16px 0;
	color: #110c22;
	color: var(--dark);
}

#feedback p {
	font-family: "IBM Plex Sans", sans-serif;
	font-family: var(--ff-body);
	font-style: normal;
	font-weight: 400;
	font-size: 18px;
	line-height: 160%;
	margin-bottom: 16px;
	text-align: center;
}

#feedback .layout-medium {
	width: 550px;
	height: auto;
}

#feedback .layout-medium .content {
	align-items: flex-start;
	padding: 32px;
}

#feedback .emojis {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0 0 32px;
}

#feedback .eachEmoji {
	position: relative;
	font-size: 2em;
	margin: 20px;
	z-index: 1;
	vertical-align: middle;
	cursor: pointer;
	-webkit-filter: grayscale(1);
	        filter: grayscale(1);
	opacity: 0.8;
}

#feedback .eachEmoji:hover {
	-webkit-filter: grayscale(0);
	        filter: grayscale(0);
	opacity: 1;
}

#feedback .eachEmoji::before {
	position: absolute;
	content: "";
	width: 70px;
	height: 70px;
	border-radius: 50%;
	display: inline-block;
	z-index: -10;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

#feedback .eachEmoji:hover::before {
	cursor: pointer;
	background: hsl(0, 0%, 95%);
	background: var(--grey);
}

#feedback #users-feedback {
	width: 100%;
	min-height: 150px;
	height: auto;
	padding: 16px;
	margin-bottom: 32px;
	font-size: 18px;
	border: 2px solid hsl(224, 100%, 62%);
	border: 2px solid var(--primary);
	border-radius: 5px;
}

#feedback .user-actions {
	margin: 32px 0 0;
}

@media screen and (max-width: 640px) {
	#feedback main {
		padding: 16px;
		margin: 0;
	}

	#feedback .layout-medium {
		width: 90%;
	}

	#feedback .layout-medium .content {
		padding: 16px;
	}

	#feedback .eachEmoji {
		font-size: 1em;
	}

	#feedback .eachEmoji::before {
		width: 45px;
		height: 45px;
	}

	#feedback .user-actions {
		margin-top: 16px;
	}
}

:root {
  --primary-clr: #00b0f0;
  --secondary-clr: #f1540f;
}

#contribution p#break {
  font-weight: lighter;
  font-size: 0.85rem;
  line-height: 1;
  margin: 0;
}

#contribution .container {
  width: 75%;
  margin: auto;
  max-width: 700px;
}

#contribution .card {
  box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.46);
  border-radius: 10px;
  margin-bottom: 2em;
  padding: 2em;
  background: rgba(247, 247, 247, 0.95);
}

#contribution .message-title {
  margin-top: 0;
  padding-bottom: 0.75em;
  border-bottom: solid 1px rgb(221, 221, 221);
  text-align: left;
}

#contribution .message-container {
  margin-bottom: 8em;
}

#contribution form {
  display: grid;
  grid-template-columns: 90px auto auto;
  grid-column-gap: 0.5em;
  grid-row-gap: 2em;
}

#contribution label {
  font-weight: bold;
  grid-column: 1/2;
}

#contribution input {
  grid-column: 2/4;
  height: 3em;
  border: solid 3px #00b0f0;
  border: solid 3px var(--primary-clr);
  border-radius: 4em;
  padding: 0 1em;
}

#contribution input,
#contribution textarea {
  font-family: "Roboto", sans-serif;
}

#contribution textarea {
  grid-column: 2/4;
  border-radius: 2em;
  border: solid 3px #00b0f0;
  border: solid 3px var(--primary-clr);
  padding: 0.5em 1em;
}

#contribution input:focus,
#contribution textarea:focus {
  background: rgb(211, 230, 253);
}

#contribution .first-name {
  grid-column: 2/3;
}

#contribution .last-name {
  grid-column: 3/4;
}

#contribution .button-container {
  grid-column: 1/4;
  display: flex;
  justify-content: center;
  
}

#contribution .button {
  width: 100px;
  background: black;
  color: white;
  border: none;
  cursor: pointer;
  transition: .4s ease;
}

#contribution .button:hover {
  opacity: 0.7;
}


/* For Mobile */
@media (max-width: 750px) {
  #contribution form {
    display: flex;
    flex-direction: column;
    grid-row-gap: 0;
  }

  #contribution .name {
    grid-column-gap: 0;
  }

  #contribution input,
  #contribution textarea {
    margin: 1em 0;
  }

  #contribution p#break {
    display: inline;
  }
}

